.c-stepper__item {
  gap: 1rem;
  padding-bottom: 4rem;
  display: flex;
  position: relative;
}

.c-stepper__item:before {
  content: "";
  flex: 0 0 var(--circle-size);
  height: var(--circle-size);
  background-color: #d3d3d3;
  border-radius: 8px;
}

.c-stepper__item.active:before {
  background-color: #4286f4;
}

.c-stepper__item.completed:before {
  background-color: #2ecc71;
}

.c-stepper__item:not(:last-child):after {
  content: "";
  left: 0;
  top: calc(var(--circle-size)  + var(--spacing));
  bottom: var(--spacing);
  z-index: -1;
  transform: translateX(calc(var(--circle-size) / 2));
  background-color: #d3d3d3;
  width: 3px;
  position: absolute;
}

.c-stepper__item:not(:last-child).active:after {
  background-color: #4286f4;
}

.c-stepper__item:not(:last-child).completed:after {
  background-color: #2ecc71;
}

